img {
  width: 30vw;
  min-width: 300px;
  margin: 80px;
  background-size: cover;
  -webkit-filter: blur(30px);
          filter: blur(30px);
  -webkit-transition: 1s linear filter;
  -o-transition: 1s linear filter;
  transition: 1s linear filter;
}

img.ready {
  -webkit-filter: blur(0px);
          filter: blur(0px);
}



